import moment from 'moment'

import CoolDown from '../models/CoolDown'

import A1_fetch_jsonOdd_eventOdds from '../updateDB/A1_fetch_jsonOdd_eventOdds';
import A2_fetch_pickMon_eventOdds from '../updateDB/A2_fetch_pickMon_eventOdds';

import B1_build_SportLeague_table from '../updateDB/B1_build_SportLeague_table';

import C1_fetch_jsonOdd_result from '../updateDB/C1_fetch_jsonOdd_result';
import C2_fetch_pickMon_result from '../updateDB/C2_fetch_pickMon_result';
import D1_sync_Result_to_openBet_eventOdds from '../updateDB/D1_sync_Result_to_openBet_eventOdds';
import E1_determine_OpenBet_eventOdds_result from '../updateDB/E1_determine_OpenBet_eventOdds_result';
import F1_confirm_openBet_result_to_HistoryBet from '../updateDB/F1_confirm_openBet_result_to_HistoryBet';

export const fetchAllEventOdds_MW = async (req, res, next) => {
	try{
//		const cooldown = await CoolDown.findOne({ usage: 'fetchAllEventOdds' })
		const cooldown = await CoolDown.findOneAndUpdate({ usage: 'fetchAllEventOdds' }, { $set: { updatedAt: moment() } })
		if(moment().format('X') - moment(cooldown.updatedAt).format('X') > cooldown.sec){
//			await CoolDown.findOneAndUpdate({ usage: 'fetchAllEventOdds' }, { $set: { updatedAt: moment() } })
			await A1_fetch_jsonOdd_eventOdds()
			await A2_fetch_pickMon_eventOdds()
		}
		next()
	}catch(err){
		throw err
	}
}

export const buildSportLeagueTable_MW = async (req, res, next) => {
	try{
		await B1_build_SportLeague_table()
		next()
	}catch(err){
		throw err
	}
}

export const fullResultUpdateDB_MW = async ( req, res, next) => {
	try{
//		const cooldown = await CoolDown.findOne({ usage: 'fullResultUpdateDB' })
		const cooldown = await CoolDown.findOneAndUpdate({ usage: 'fullResultUpdateDB' }, { $set: { updatedAt: moment() } })
		if(moment().format('X') - moment(cooldown.updatedAt).format('X') > cooldown.sec){
//			await CoolDown.findOneAndUpdate({ usage: 'fullResultUpdateDB' }, { $set: { updatedAt: moment() } })
			await C1_fetch_jsonOdd_result()
			await C2_fetch_pickMon_result()
			await D1_sync_Result_to_openBet_eventOdds()
			await E1_determine_OpenBet_eventOdds_result()
			await F1_confirm_openBet_result_to_HistoryBet()
		}
		next()
	}catch(err){
		throw err
	}
}

